import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import ceshi1 from '@/components/ceshi1'
import ceshi2 from '@/components/ceshi2'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/ceshi1',
      name: 'ceshi1',
      component: ceshi1
    },
    {
      path: '/ceshi2',
      name: 'ceshi2',
      component: ceshi2
    }
  ]
})



